import http from '../httpConfig'
const table = 'mrbase_sys_org_levels'
// 

// 
export const getChooseStaffTreeList = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/select/list-organization/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

