/*
* @params title 
* @params arr rule
*/
export const text_rules = (title, arr = [], len = 50) => {
  return [
    v => !!v || title,
    ...arr,
    v => String(v).length <= len || '',
    // v => !/[@#$%^&()<>?/]/.test(v) || '',
    v => !/^ +| +$/g.test(v) || '',
  ]
}
