import Cookie from 'js-cookie'
import { setup_locale } from '@/lang';
const mutations = {
  // token
  set_token (state, value) {
    Cookie.set('token', value, { expires: 1 / 12 })
    state.token = value
  },
  //  
  set_sideMenu (state, value) {
    sessionStorage.setItem('sideMenu', JSON.stringify(value))
    state.sideMenu = value
  },
  set_userData (state, value) {
    localStorage.setItem('userData', JSON.stringify(value))
    state.userData = value
  },
  set_topcompanylist (state, value) {
    sessionStorage.setItem('topcompanylist', JSON.stringify(value))
    state.topcompanylist = value
  },
  set_miniVariant (state, value) {
    state.miniVariant = value
  },
  set_iconstatus (state, value) {
    localStorage.setItem('iconstatus', JSON.stringify(value))
    state.iconstatus = value
  },
  set_subPage_name (state, value) {
    localStorage.setItem('subPage_name', JSON.stringify(value))
    state.subPage_name = value
  },
  set_product_uuid (state, value) {
    localStorage.setItem('product_uuid', JSON.stringify(value))
    state.product_uuid = value
  },
  set_uuid (state, value) {
    localStorage.setItem('uuid', JSON.stringify(value))
    state.uuid = value
  },
  set_uuid2 (state, value) {
    localStorage.setItem('uuid2', JSON.stringify(value))
    state.uuid2 = value
  },
  set_client_type (state, value) {
    localStorage.setItem('client_type', JSON.stringify(value))
    state.client_type = value
  },
  set_version (state, value) {
    localStorage.setItem('version', JSON.stringify(value))
    state.version = value
  },
  set_currentCompany (state, value) {
    if (value) {
      localStorage.setItem('currentCompany', JSON.stringify(value))
      state.currentCompany = value
    } else {
      localStorage.setItem('currentCompany', JSON.stringify(''))
      state.currentCompany = ''
    }

  },
  // id
  set_detailuuid (state, value) {
    localStorage.setItem('detailuuid', JSON.stringify(value))
    state.detailuuid = value
  },
  set_detailclientname (state, value) {
    localStorage.setItem('detailclientname', JSON.stringify(value))
    state.detailclientname = value
  },
  //  
  set_clientcontacts (state, value) {
    state.clientcontacts = value
  },
  newsTips (state, value) {
    state.snackbar_open = true
    state.newsText = value
  },
  set_change (state, value) {
    localStorage.setItem('is_change', value)
    state.is_change = value
  },
  set_nested_flag (state, value) {
    state.nested_flag = value
  },
  set_loginStatus (state, value) { //
    state.loginStatus = value
  },
  // 
  set_lang (state, value) {
    state.lang = value.value.toLowerCase()
    state.lang_name = value.text
    localStorage.setItem('localeLanguage', value.value.toLowerCase())
    localStorage.setItem('lang_name', value.text)
    state.currentCompanyLanguage.forEach(item => {
      if (item.value.toLowerCase() == value.value.toLowerCase()) {
        item.is_default = 1
      } else {
        item.is_default = 0
      }
    })
    localStorage.setItem('companyLang', JSON.stringify(state.currentCompanyLanguage))
    setup_locale(value.value.toLowerCase())

  },
  setTab (state, value) {
    localStorage.setItem('tab', JSON.stringify(value))
    state.tab = value
  },
  setTab2 (state, value) {
    localStorage.setItem('tab2', JSON.stringify(value))
    state.tab2 = value
  },
  setOrgLevelCode (state, value) {
    localStorage.setItem('orgLevelCode', JSON.stringify(value))
    state.orgLevelCode = value
  },
  // setAllLanguage (state, value) {
  //   localStorage.setItem('allLanguage', JSON.stringify(value))
  //   state.allLanguage = value
  // },
  setCurrentCompanyLanguage (state, value) {
    localStorage.setItem('currentCompanyLanguage', JSON.stringify(value))
    state.currentCompanyLanguage = value
  },
  set_languageList (state, value) {
    // console.log(value);
    state.languageList = value
    localStorage.setItem('languageList', JSON.stringify(value))
  },
  setCurrentCopmpanyMenu (state, value) {
    localStorage.setItem('currentCopmpanyMenu', JSON.stringify(value))
    state.currentCopmpanyMenu = value
  },
  setMenuFunctionUuid (state, value) {
    localStorage.setItem('menuFunctionUuid', value)
    state.menuFunctionUuid = value
  },
  setBtnPermission (state, value) {
    localStorage.setItem('btnPermission', JSON.stringify(value))
    state.btnPermission = value
  },
  set_currentCompanyClientUuid (state, value) {
    localStorage.setItem('currentCompanyClientUuid', value)
    state.currentCompanyClientUuid = value
  },
  // state
  resetState (state) {
    const obj = {
      token: Cookie.get('token') || '', // token
      sideMenu: JSON.parse(sessionStorage.getItem('sideMenu')) || [], // 
      userData: JSON.parse(localStorage.getItem('userData')) || {}, // 
      miniVariant: false, // 
      product_uuid: JSON.parse(localStorage.getItem('product_uuid')) || '', //id
      subPage_name: JSON.parse(localStorage.getItem('subPage_name')) || '', // null
      uuid: JSON.parse(localStorage.getItem('uuid')) || '',// null
      uuid2: JSON.parse(localStorage.getItem('uuid2')) || '',// null
      currentCompany: JSON.parse(localStorage.getItem('currentCompany')) || '',//company
      is_change: localStorage.getItem('is_change') || '', // 1.   
      snackbar_open: false,
      snackbar_timeout: 1500,
      newsText: '',
      nested_flag: false, //  
      lang: localStorage.getItem('localeLanguage') || navigator.language.toLowerCase() || navigator.userLanguage.toLowerCase(), // 
      lang_name: localStorage.getItem('lang_name') || '',
      detailuuid: JSON.parse(localStorage.getItem('detailuuid')) || '', //uuidnull
      clientcontacts: {}, //  
      detailclientname: JSON.parse(localStorage.getItem('detailclientname')) || '',  //clientname
      iconstatus: JSON.parse(localStorage.getItem('iconstatus')) == false ? false : true,//   
      tab: JSON.parse(localStorage.getItem('tab')) || 0, // tab 
      tab2: JSON.parse(localStorage.getItem('tab2')) || 0,
      orgLevelCode: JSON.parse(localStorage.getItem('orgLevelCode')) || null,
      topcompanylist: JSON.parse(sessionStorage.getItem('topcompanylist')) || [],
      pageSize: 10,// table 
      cpage: JSON.parse(sessionStorage.getItem('cpage')) || 1,
      client_type: JSON.parse(localStorage.getItem('client_type')) || '',
      version: JSON.parse(localStorage.getItem('version')) || '',
      // allLanguage: JSON.parse(localStorage.getItem('allLanguage')) || [],
      currentCompanyLanguage: JSON.parse(localStorage.getItem('currentCompanyLanguage')) || [],
      loginStatus: undefined, //
      languageList: JSON.parse(localStorage.getItem('languageList')) || {}, // 
      currentCopmpanyMenu: JSON.parse(localStorage.getItem('currentCopmpanyMenu')) || [], // 
      menuFunctionUuid: localStorage.getItem('menuFunctionUuid') || '', // 
      currentCompanyClientUuid: localStorage.getItem('currentCompanyClientUuid') || ''
    }
    for (var i in obj) {
      state[i] = obj[i]
    }
  },
  setCalcHeight (state, value) {
    state.calcHeight = value
  },
  setCpage (state, value) {
    sessionStorage.setItem('cpage', JSON.stringify(value))
    state.cpage = value
  },
  setPagesize (state, value) {
    sessionStorage.setItem('pageSize', JSON.stringify(value))
    state.pageSize = value
  },
}

export default mutations