import http from '../httpConfig'
// 

// 
export const getList = (params) => {
    return new Promise((resolve, reject) => {
        http.post('/api/dictionary/list-dictionary/v1', params)
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                reject(err)
            })
    })
}
